import { Component, OnInit } from '@angular/core';


declare var $:any;

@Component({
  selector: 'app-pedido-venda',
  templateUrl: './pedido-venda.component.html',
  styleUrls: ['./pedido-venda.component.css']
})
export class PedidoVendaComponent implements OnInit {

  constructor() { }

  ngOnInit() {


    
    //concertar menu na troca de página
    $(document).ready(function(){
      if ($(".navbar-primary").hasClass("collapsed")) {
        $(".main-content").addClass('main-content2');
      }
    });


    //search box
    $( ".campo-busca" ).focus(function(e) {
      e.preventDefault();
      $(".search_box").toggleClass('active-search');
    });

    $( ".campo-comprar" ).click(function(e) {
      e.preventDefault(); 
      $(".search_box").toggleClass('active-search');
    });

  


//adicionar junto
$( ".campo-comprar" ).click(function(e) {
  e.preventDefault();
  $(".adicionar-junto").toggleClass('active-adicionar-junto');
});

$( ".campo-comprar2" ).click(function(e) {
  e.preventDefault();
  $(".adicionar-junto").toggleClass('active-adicionar-junto');
});



//DRAGGABLE
$( function() {
$( "#draggable" ).draggable();
} );






  }

}
